import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { EnvironmentService, AuthenticationService } from 'src/app/core/services';
import { CookiesService } from 'src/app/core/services/cookies/cookies.service';

@Injectable()
export class TagsService {
    

    constructor(
        public http: HttpClient,
        public envService: EnvironmentService,
        public cookies: CookiesService,
        public authentication: AuthenticationService
    ){}

    public getTags(): any{
        return new Promise((resolve,reject) => {
            this.http.get(this.envService.read('apiUrl') +'/'+ window['instance_id'] + '/tags/global').subscribe(resolve, reject);
        });  
    }

    public deleteTag(id): any{
        let tags = [];
        tags.push(id);
        // return new Promise((resolve,reject) => {
        //     this.http.delete(this.envService.read('apiUrl') +'/'+ window['instance_id'] + '/tags/global',{""tags}).subscribe(resolve, reject);
        // });  
    }

    // public editTag(data,id):any{
    //     return new Promise((resolve,reject) => {
    //         this.http.get(this.envService.read('apiUrl') +'/'+ window['instance_id'] + '/tags/global').subscribe(resolve, reject);
    //     });
    // }

    public createTag(data): any{
        return new Promise((resolve,reject) => {
            this.http.patch(this.envService.read('apiUrl') +'/'+ window['instance_id'] + '/tags/global',data).subscribe(resolve, reject);
        });  
    }




}


